<template>
  <div class="g-shop-similar">
    <div class="head clear">
      <h2>热门菜谱</h2>
    </div>
 <div class="food_menu">
 <router-link :to="'shop/0/01/1494153625921'">  
  <mt-swipe :auto="0" :show-indicators="false" >
    <mt-swipe-item>
      <img src="../assets/one.png" alt="">
      <img src="../assets/two.png" alt="">
      <img src="../assets/three.png" alt="">
    </mt-swipe-item>
    <mt-swipe-item>
      <img src="../assets/four.png" alt="">
      <img src="../assets/five.png" alt="">
      <img src="../assets/six.png" alt="">
    </mt-swipe-item>
   </mt-swipe>
  </router-link>
  </div>  
  </div>
</template>

<script>
//00.引入Vue对象
import Vue from 'vue';
//01.引入mint-ui组件
//js引入
import { Swipe, SwipeItem } from 'mint-ui';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

//02.引入mint-ui  的css
import 'mint-ui/lib/style.css'
// import Swiper from 'swiper'
// import VueResource from 'vue-resource';
// Vue.use(VueResource);
export default {
  data(){
    return {
      listData: [],
    }
  },
  methods:{
    fetchData(){
      var _this = this;
      this.$http.get('/static/Recommend.json').then(function(res){
        _this.listData = res.data;
        if (_this.listData.length == 0) {
          _this.NoDataState = true;
        }
      }).catch(function(err) {
        console.log(err);
      });
    }
  },
  mounted(){
    this.fetchData(); //获取数据
  },
}

</script>
<style lang='stylus' scoped>
@import '../assets/common/stylus/common.styl'
.g-shop-similar
   width pxtorem(750px)
   height pxtorem(290px)
   margin-top pxtorem(15px)
   background white  
  .mint-swipe-item
    width pxtorem(750px)
   .mint-swipe-items-wrap
     width pxtorem(200px)
    .mint-swipe
     height pxtorem(200px)
     margin-left pxtorem(15px)
     margin-right pxtorem(15px)
    .mint-swipe-item
       img
        width pxtorem(235px)
        margin-left pxtorem(5px)
        height pxtorem(200px)
    .clear
      clear both
    .head
      height pxtorem(70px)
      line-height pxtorem(70px)
     h2
      float left
      font-size pxtorem(30px) 
     .more
      float right
      font-size pxtorem(30px)
  // .food_menu
    
</style>



